@import "colors";
.ui-footer {
  position: relative;
  z-index: 1;
  padding-top: 70px;
  color: $black_color;
  background-color: white;

  img {
    max-width: 160px;
  }

  .ui-footer-title {
    font-weight: bold;
    font-size: 1.125em;
    margin-bottom: 1.5rem;
  }

  ul li {
    margin: 10px 0;

    i {
      display: inline-block;
      vertical-align: middle;
      min-width: 25px;
      text-align: center;
    }
  }

  .social {
    display: inline-block;
    text-align: center;
    width: 35px;
    height: 35px;
    border-radius: 50%;
    line-height: 40px;
    font-size: 1.25rem;
    color: $white_color;
    background-color: $black_color;
    margin-left: 10px;
    overflow: hidden;
    @include transition();

    &:hover {
      background-color: $light_blue_color;
    }
  }

  .copy-right {
    text-align: center;
    padding-top: 25px;
    margin-top: 25px;
    padding-bottom: 25px;
    border-top: 1px solid rgba($color: #000000, $alpha: 0.15);
  }
}
