@import "colors";
#open-menu {
  display: none;
  background-color: transparent;
  color: white;
  outline: 0;
  border: none;
  font-size: 34px;
}

.ui-header {
  // position: absolute;
  position: fixed;
  right: 0;
  left: 0;
  top: 0;
  margin: auto;
  padding: 10px 0;
  z-index: 2;
  @include gradient();

  .container {
    width: 86%;
    max-width: 1340px;
  }

  .overal {
    position: fixed;
    right: 0;
    top: 0;
    width: 100%;
    height: 100%;
    margin: auto;
    background-color: rgba($color: #101010, $alpha: 0.85);
    z-index: 14;
    cursor: url(../images/close.png), auto;
    visibility: hidden;
    opacity: 0;
    @include transition($property: 0.75s);

    &.show {
      visibility: visible;
      opacity: 1;
    }
  }

  .menu-aside {
    position: fixed;
    top: 0;
    bottom: 0;
    margin: auto;
    right: 0;
    width: 320px;
    background-color: $white_color;
    padding: 30px;
    z-index: 15;
    overflow: auto;
    -webkit-transition: all 900ms cubic-bezier(0.99, 0.015, 0.195, 0.99);
    -moz-transition: all 900ms cubic-bezier(0.99, 0.015, 0.195, 0.99);
    -o-transition: all 900ms cubic-bezier(0.99, 0.015, 0.195, 0.99);
    transition: all 900ms cubic-bezier(0.99, 0.015, 0.195, 0.99);
    -webkit-transition-timing-function: cubic-bezier(0.99, 0.015, 0.195, 0.99);
    -moz-transition-timing-function: cubic-bezier(0.99, 0.015, 0.195, 0.99);
    -o-transition-timing-function: cubic-bezier(0.99, 0.015, 0.195, 0.99);
    transition-timing-function: cubic-bezier(0.99, 0.015, 0.195, 0.99);
    @include transform($property: translateX(321px));

    .menu-logo {
      text-align: center;
      margin-bottom: 2rem;

      img {
        display: inline-block;
        height: auto;
        max-height: 40px;
      }
    }

    ul {
      li {
        a {
          color: $black_color;
          display: flex;
          align-items: center;
          justify-content: space-between;
          padding: 5px 0;
          text-decoration: none;
          font-weight: bold;
          @include transition();

          &:hover {
            color: $light_blue_color;
          }
        }

        ul {
          padding: 5px 0 5px 15px;

          li {
            a {
              color: $black_color;
              font-weight: 400;
              text-shadow: 0 0 1px;
            }

            ul li a {
              font-weight: 300;
            }
          }
        }

        &.has-children {
          & > a::after {
            content: "";
            background-image: url("data:image/svg+xml;charset=UTF-8,%3Csvg%20width%3D%2214%22%20height%3D%2214%22%20viewBox%3D%220%200%2014%2014%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%0A%20%20%20%20%3Cpolyline%20fill%3D%22none%22%20stroke%3D%22rgba%28255,%20255,%20255,%200.7%29%22%20stroke-width%3D%221.1%22%20points%3D%2210%201%204%207%2010%2013%22%20%2F%3E%0A%3C%2Fsvg%3E");
            background-repeat: no-repeat;
            background-position: 50% 50%;
            float: right;
            background-color: #777;
            width: 2em;
            height: 2em;
            @include transform($property: rotate(-90deg));
          }

          & > ul {
            display: none;
          }

          &:hover {
            & > ul {
              display: block;
            }
          }
        }
      }
    }

    &.show {
      @include transform($property: translateX(0px));
    }
  }

  .row {
    align-items: center;
  }

  .logo img {
    max-height: 50px;
  }

  .hamburger {
    padding: 0;

    .hamburger-box {
      vertical-align: middle;
    }

    &:focus {
      outline: 0;
    }

    .hamburger-inner {
      background-color: $white_color;

      &:after,
      &:before {
        background-color: $white_color;
      }
    }

    &.is-active {
      .hamburger-inner {
        background-color: $white_color;

        &::after,
        &::before {
          background-color: $white_color;
        }
      }
    }

    i {
      display: inline-block;
      vertical-align: middle;
      font-style: normal;
      color: $white_color;
      margin-right: 5px;
      font-weight: bold;
    }
  }

  .menu-content {
    text-align: center;

    ul {
      li {
        display: inline-block;
        text-align: right;
        position: relative;
        margin-left: 0.65rem;

        a {
          color: $white_color;
          background-color: transparent;
          border-radius: 32px;
          padding: 6px 17px;
          font-weight: bold;
          @include transition();
          text-decoration: none;

          &:hover {
            background-color: rgba(255, 255, 255, 0.2);
          }
        }

        &.active a {
          background-color: $white_color;
          color: $black_color;
        }

        ul {
          position: absolute;
          right: 0;
          top: 120%;
          background-color: $white_color;
          padding: 10px 0;
          @include transition();
          @include transform($property: translateY(20px));

          li {
            margin-left: 0;
            display: block;

            a {
              font-weight: 400;
              color: $black_color;
            }

            ul {
              right: 100%;
              top: 0;
            }
          }
        }

        &:last-child {
          margin-left: 0;
        }

        &:hover {
          & > ul {
            @include transform($property: translateY(0px));
          }
        }
      }
    }
  }

  .redirect {
    text-align: left;
    a {
      display: inline-block;
      vertical-align: middle;
      padding: 7px 20px;
      border-radius: 32px;
      background: #f1c40f;
      color: #000;
      text-decoration: none;
      @include transition();

      i {
        display: inline-block;
        vertical-align: middle;
        margin-left: 3px;
      }

      &:hover {
        background-color: #c4e538;
      }
    }
  }
}
