@import "../colors";
.intro-wrapper {
  padding-top: 110px;
  @include gradient();

  .row {
    align-items: center;
  }

  .container {
    position: relative;
    z-index: 0;
  }

  .ribbon {
    position: relative;
    z-index: 1;
    width: 100%;
    display: block;
    margin-top: -85px;
  }

  img {
    width: 100%;
  }

  .content {
    margin-top: -100px;
    color: $white_color;

    h1 {
      font-weight: bold;
      text-shadow: 0 0 1px;
      font-size: 3em;
      letter-spacing: 0.5px;

      span {
        display: inline-block;
        color: $red_color;
        text-shadow: 0px 2px 0 $white_color, -1px -1px 0 $white_color,
          1px -1px 0 $white_color, -1px 1px 0 $white_color,
          1px 1px 0 $white_color;
      }
    }

    h2 {
      font-size: 1.5em;
      margin-bottom: 2rem;
    }
  }
}
