@import url(../../shared/css/bootstrap.css);
@import url(../../shared/css/fontawesome.css);
// @import url(plugins/owl.css);

$red_color: #FF364D;
$black_color: #303030;
$white_color: #ffffff;
$text_color: #565e66;
$blue_color: #0f2e7e;
$light_blue_color: #00a8ff;
$pink_color: #f78fb3;
$purple_color: #7224a2;
$yellow_color: #DDB439;
$brown_color: #6a5f0b;
$orange_color: #FF7644;

@mixin transition($property: 0.3s) {
  transition: all $property ease-in-out;
  -moz-transition: all $property ease-in-out;
  -ms-transition: all $property ease-in-out;
  -webkit-transition: all $property ease-in-out;
  -o-transition: all $property ease-in-out;
}

@mixin transform($property: scale(1.2)) {
  transform: $property;
  -moz-transform: $property;
  -webkit-transform: $property;
  -ms-transform: $property;
  -o-transform: $property;
}

@mixin gradient {
  background: -webkit-linear-gradient(
    left,
    rgb(18, 29, 102) 0%,
    rgb(11, 70, 160) 40%,
    rgb(11, 74, 166) 92%,
    rgb(12, 66, 154) 100%
  );
  background: -o-linear-gradient(
    left,
    rgb(18, 29, 102) 0%,
    rgb(11, 70, 160) 40%,
    rgb(11, 74, 166) 92%,
    rgb(12, 66, 154) 100%
  );
  background: -ms-linear-gradient(
    left,
    rgb(18, 29, 102) 0%,
    rgb(11, 70, 160) 40%,
    rgb(11, 74, 166) 92%,
    rgb(12, 66, 154) 100%
  );
  background: -moz-linear-gradient(
    left,
    rgb(18, 29, 102) 0%,
    rgb(11, 70, 160) 40%,
    rgb(11, 74, 166) 92%,
    rgb(12, 66, 154) 100%
  );
  background: linear-gradient(
    to right,
    rgb(18, 29, 102) 0%,
    rgb(11, 70, 160) 40%,
    rgb(11, 74, 166) 92%,
    rgb(12, 66, 154) 100%
  );
}

@font-face {
  font-family: "sans";
  src: url("../fonts/IRANSansWebFaNum.eot");
  src: url("../fonts/IRANSansWebFaNum.eot?#iefix") format("embedded-opentype"),
    url("../fonts/IRANSansWebFaNum.woff2") format("woff2"),
    url("../fonts/IRANSansWebFaNum.woff") format("woff"),
    url("../fonts/IRANSansWebFaNum.ttf") format("truetype"),
    url("../fonts/IRANSansWebFaNum.svg#IRANSansWebFaNum") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "sans";
  src: url("../fonts/IRANSansWeb-Bold.eot");
  src: url("../fonts/IRANSansWeb-Bold.eot?#iefix") format("embedded-opentype"),
    url("../fonts/IRANSansWeb-Bold.woff2") format("woff2"),
    url("../fonts/IRANSansWeb-Bold.woff") format("woff"),
    url("../fonts/IRANSansWeb-Bold.ttf") format("truetype"),
    url("../fonts/IRANSansWeb-Bold.svg#IRANSansWeb-Bold") format("svg");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

input,
select,
textarea {
  font-family: "sans" !important;
}
.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: 1.65;
}
.ltr-text {
  display: inline-block;
  direction: ltr;
  text-align: right;
  letter-spacing: 0.5px;
}
html,
body {
  direction: rtl;
  font-size: 15px !important;
  font-family: "sans" !important;
  font-weight: 400 !important;
  line-height: 1.75 !important;
  text-align: right !important;
  overflow-x: hidden !important;
  height: auto !important;
}
