@media (max-width: 1031px) {
  .ui-header {
    .redirect {
      a {
        font-size: 0.8rem !important;
        padding: 7px 6px !important;
      }
    }
    .menu-content {
      ul {
        li {
          margin-left: 0.5rem;
          a {
            font-size: 0.9rem !important;
            padding: 6px 15px;
          }
        }
      }
    }
  }
}
@media (max-width: 993px) {
  .ui-header {
    .menu-content {
      display: none;
    }

    .col-lg-8 {
      display: none;
    }

    .col-lg-2 {
      display: flex;
      flex-direction: row-reverse;
      justify-content: space-between;
    }
    .logo img {
      max-height: 40px;
    }
  }

  #open-menu {
    display: block;
  }

  .wrapper {
    padding: 50px 0;
  }

  .ui-main {
    .intro-wrapper .content {
      h1 {
        font-size: 1.8em;
        margin-bottom: 0.5rem;
      }

      h2 {
        font-size: 1.05em;
      }
    }

    .why-wrapper {
      border-bottom-left-radius: 25%;

      .humanObj {
        max-height: 420px;
      }
    }

    .free-wrapper .title-box {
      margin-top: 2rem;
    }

    .amount-wrapper {
      .title-box {
        margin-top: -75px;
        width: 160px;
        height: 160px;
        margin-bottom: 1rem;

        & > div {
          font-size: 1em;

          i {
            font-size: 2em;
          }
        }
      }
    }

    .ranking-wrapper {
      .title-box {
        margin-bottom: 2rem;
      }

      .best-title {
        margin-top: 2.5rem;
        margin-bottom: 1.5rem;
      }
    }
  }

  .ui-footer {
    .ui-footer-content {
      margin-bottom: 2rem;
    }

    .copy-right {
      margin-top: 0;
    }
  }

  .girls-page {
    .ui-main .why-wrapper {
      border-bottom-left-radius: 25%;
    }
  }
}

@media (max-width: 769px) {
  .ui-header {
    .container {
      width: 96%;
    }
    .redirect a {
      padding: 5px 15px;
    }
  }
  .ui-main {
    .intro-wrapper {
      padding-top: 125px;

      .row {
        flex-direction: column-reverse;
      }

      .content {
        margin-top: 0;
        text-align: center;

        h2 {
          margin-bottom: 1.25rem;
        }

        .btn {
          margin-bottom: 2rem;
        }
      }
    }
    .why-wrapper .humanObj {
      display: none;
    }
    .free-wrapper {
      padding-top: 100px;
      padding-bottom: 75px;
    }
    .amount-wrapper {
      padding-top: 75px;

      .title-box {
        margin-top: 0;
      }

      .content-box {
        margin-bottom: 2rem;
      }

      .col-md-4:last-child {
        .content-box {
          margin-bottom: 0;
        }
      }
    }
    .comment-wrapper {
      .comment-item {
        flex-direction: column;

        .content {
          max-width: 100%;
          padding-right: 0;
        }

        &:nth-child(2n) {
          direction: rtl;

          .content {
            text-align: right;
            padding-left: 0;
          }
        }
      }

      .arrow1,
      .arrow2,
      .arrow3 {
        display: none;
      }
    }
  }
  .girls-page {
    .ui-main .intro-wrapper .row {
      flex-direction: column;
    }
  }
}

@media (max-width: 576px) {
  html,
  body {
    font-size: 14px;
  }

  .ui-header .container {
    width: 100%;
  }

  .ui-main {
    .intro-wrapper img {
      width: 100%;
      display: table;
      margin: auto;
    }

    .why-wrapper {
      border-bottom-left-radius: 0;
      padding-bottom: 10px;
    }

    .teacher-wrapper {
      margin-top: 0;
      padding-top: 40px !important;

      // .slide-content {
      //   padding: 0px 100px 50px 100px;
      // }

      .owl-theme .owl-nav {
        .owl-prev {
          right: -100px;
        }
        .owl-next {
          left: -100px;
        }
      }
    }

    .ranking-wrapper .ranking-carousel {
      max-width: 60%;
      margin: auto;
    }

    .time-wrapper .title-section {
      font-size: 1.25em;
      margin-right: 0;
    }

    .free-wrapper {
      padding-bottom: 50px;
    }
    .amount-wrapper {
      padding-bottom: 50px;
      padding-top: 50px;
    }

    .social-wrapper {
      padding-bottom: 100px;

      .section-title {
        margin-right: 15px;
      }
    }
  }

  .ui-footer {
    padding-top: 30px;
    .social {
      margin-bottom: 15px !important;
    }
  }

  .girls-page {
    .ui-main .why-wrapper {
      border-bottom-right-radius: 0;
    }
  }
}
