@import "../colors";
.teacher-wrapper {
  position: relative;
  z-index: 0;
  margin-top: -280px;
  padding-top: 350px !important;
  @include gradient();
  color: $white_color;

  .title-box {
    text-align: center;
    color: $white_color;
    margin-bottom: 2rem;

    .title {
      position: relative;
      display: inline-block;
      background-color: $white_color;
      padding-right: 15px;
      padding-left: 5px;
      height: 50px;
      line-height: 51px;
      font-size: 1.35em;
      font-weight: bold;
      margin-right: -50px;
      color: $blue_color;
      border-top-right-radius: 6px;
      border-bottom-right-radius: 6px;
      margin-bottom: 0.75rem;

      span {
        position: relative;
        z-index: 1;
        display: inline-block;
      }

      &::before {
        content: "";
        position: absolute;
        z-index: 0;
        left: -120px;
        top: -1px;
        width: 150px;
        height: 51px;
        background: url("../../images/title-ribbon-white.png") no-repeat;
        background-size: 100% 100%;
      }
    }

    .desc {
      font-size: 1.125em;
    }
  }

  .slide-content {
    border-top: 2px solid $white_color;
    // padding: 50px 125px 50px 125px;
    padding: 20px 5px 50px 5px;

    .teacher-item {
      position: relative;
      background-color: $white_color;
      border-radius: 6px;
      padding: 10px;
      padding-bottom: 20px;
      text-align: center;
      @include transition();

      // &::before {
      //   content: "";
      //   position: absolute;
      //   left: 0;
      //   right: 0;
      //   top: -65px;
      //   margin: auto;
      //   background: url("../../images/sanjagh.png") no-repeat;
      //   width: 42px;
      //   height: 70px;
      // }

      // &::after {
      //   content: "";
      //   position: absolute;
      //   left: 0;
      //   right: 0;
      //   top: -175px;
      //   margin: auto;
      //   width: 2px;
      //   height: 110px;
      //   background-color: $white_color;
      // }

      a {
        color: $black_color;
      }

      img {
        display: block;
        width: 100%;
        margin-bottom: 1rem;
        border-radius: 10px;
      }

      .title {
        font-weight: bold;
        font-size: 1.25em;
      }

      &:hover {
        box-shadow: 0 0 15px rgba($color: $white_color, $alpha: 0.5);
      }
    }
  }

  .owl-carousel {
    .owl-stage-outer {
      padding-top: 175px;
      padding-bottom: 25px;
    }
  }

  .owl-theme .owl-nav {
    margin-top: 0;

    [class*="owl-"] {
      margin: 0;
      position: absolute;
      top: -19px;
      font-size: 2.5em;
      @include transition();
      line-height: 0;

      &:hover {
        background: transparent;
        color: #7ed6df;
        outline: 0;
      }

      &:focus {
        outline: 0;
      }
    }

    .owl-prev {
      right: -150px;
    }

    .owl-next {
      left: -150px;
    }
  }

  .gEIJqc {
    color: $white_color;

    &:hover:enabled,
    &:focus:enabled {
      background-color: $red_color;
    }
  }

  // .rec-pagination {
  //   display: none;
  // }
}
